import {
  createContext,
  type FC,
  type ReactNode,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { NetworkStatus } from '@apollo/client';
import { isEqual } from 'lodash';
import {
  type PostOutput,
  PostVisibility,
  SortOperator,
  SpaceTypeEnum,
  type UserOutput,
} from '@/apollo/generated/types';
import {
  type SpaceOutputFragment,
  useDeletePostMutation,
  useGetAllUidForChamberPostsQuery,
  useGetPostsByChamberIdQuery,
  useUserPostsQuery,
} from '@/apollo/graphql';
import { cleanList } from '@/utils/list';
import useEvent from '@/hooks/useEvent';
import { useAuth } from '@/features/auth/contexts';
import { useLaunchDarkly } from '@/hooks';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { trimAndLowerString } from '@/utils/strings';
import { type DropdownValueType } from '@/components/Dropdown/types';
import { getFullName } from '@/utils/fullName';
import { Avatar } from '@/components/Avatar/Avatar';
import useDebounce from '@/hooks/useDebounce';
import { type FilterType, type IPostElementContext } from './types';

const initialValue: IPostElementContext = {
  loading: false,
  isDeleting: false,
  isCollapse: true,
  isCollapseDisabled: false,
  posts: [],
  allPostsLoading: true,
  setIsCollapse: () => {},
  showCreate: false,
  setShowCreate: () => {},
  filter: {},
  setFilter: () => {},
  setLoading: () => {},
  deletePost: async () => false,
  showFilter: false,
  setShowFilter: () => {},
  authors: [],
  refetchPosts: () => {},
  fetchMore: () => {},
  setAllPostsLoading: () => {},
  isSpaceOwner: false,
  space: undefined,
  networkStatus: NetworkStatus.ready,
  totalCount: 0,
  noumEditor2: false,
  isCustomPreview: false,
  isMasterNoum: false,
  authorFilterKeyword: '',
  setAuthorFilterKeyword: () => {},
  authorFilterOptions: [],
};

export const PAGE_SIZE = 10;
const SORT_DEFAULT = {
  sort: {
    column: 'createdAt',
    operator: SortOperator.Desc,
  },
};

const FILTER_DEFAULT = {
  filter: {
    visibility: [PostVisibility.Connection, PostVisibility.Follower],
  },
};

const allOption: DropdownValueType<string> = {
  type: 'value',
  value: 'all',
  key: 'all_authors',
  label: 'All',
};

export const PostElementContext =
  createContext<IPostElementContext>(initialValue);

export const PostElementProvider: FC<{
  children: ReactNode;
  space?: SpaceOutputFragment;
  isCustomPreview?: boolean;
}> = ({ children, space, isCustomPreview }) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const [loading, setLoading] = useState<boolean>(false);
  const [allPostsLoading, setAllPostsLoading] = useState<boolean>(true);
  const { user } = useAuth();
  const [isCollapse, setIsCollapse] = useState<boolean>(
    !(noumEditor2 && !isCustomPreview),
  );

  const isOwner = user?._id === space?.uid?._id;
  const [isCollapseDisabled, setIsCollapseDisabled] = useState<boolean>(true);
  const [showCreate, setShowCreate] = useState<boolean>(false);
  const [posts, setPosts] = useState<PostOutput[]>([]);
  const [totalCount, setTotalCount] = useState(0);
  const [networkStatus, setNetworkStatus] = useState(NetworkStatus.ready);
  const [filter, setFilter] = useState<FilterType>({
    ...SORT_DEFAULT,
    ...FILTER_DEFAULT,
  });
  const [showFilter, setShowFilter] = useState<boolean>(false);

  const spaceId = space?._id ?? '';
  const isSpaceOwner = space?.uid?._id === user?._id;

  const [deletePostMutation, { loading: isDeleting }] = useDeletePostMutation();
  const {
    data: authorData,
    loading: loadingAuthors,
    refetch: refetchAuthors,
  } = useGetAllUidForChamberPostsQuery({
    variables: {
      chamberId: spaceId,
    },
    fetchPolicy: 'cache-and-network',
    skip: !spaceId,
  });

  const authors = useMemo(
    () => cleanList(authorData?.getAllUidForChamberPosts?.data),
    [authorData?.getAllUidForChamberPosts?.data],
  );

  const { isConnected, isFollowing, loadingSpace } = useNoumContext();

  const homeNoumUser = useMemo(() => {
    if (space?.type === SpaceTypeEnum.Home) return space.uid;
    return undefined;
  }, [space]);

  const allowToViewNoumPosts = useMemo(
    () =>
      window.location.pathname.includes(`posts/${spaceId}`) ||
      (!!spaceId &&
        !loadingSpace &&
        (isOwner || (!isOwner && (isConnected || isFollowing)))),
    [isConnected, isFollowing, isOwner, loadingSpace, spaceId],
  );

  const skipToFetchNoumPosts = useMemo(
    () => spaceId === '' || !!homeNoumUser || !allowToViewNoumPosts,
    [allowToViewNoumPosts, homeNoumUser, spaceId],
  );

  const {
    data,
    loading: loadingPosts,
    fetchMore: fetchMorePosts,
    refetch,
    networkStatus: networkStatusPosts,
  } = useGetPostsByChamberIdQuery({
    variables: {
      chamberId: spaceId,
      filter: filter.filter,
      sort: filter.sort || SORT_DEFAULT.sort,
      limit: PAGE_SIZE,
    },
    fetchPolicy: 'cache-and-network',
    errorPolicy: 'all',
    skip: skipToFetchNoumPosts,
  });

  const {
    data: byAuthorData = {},
    loading: loadingUsersPosts,
    refetch: refetchUserPosts,
    fetchMore: fetchMoreUserPosts,
    networkStatus: networkStatusUsersPosts,
  } = useUserPostsQuery({
    variables: { uid: homeNoumUser?._id || '', limit: PAGE_SIZE },
    skip: !homeNoumUser?._id,
    fetchPolicy: 'cache-and-network',
  });
  const { userPosts } = byAuthorData;

  const deletePost = useCallback(
    async (id) => {
      let isSuccess;
      try {
        await deletePostMutation({
          variables: {
            _id: id,
          },
        });
        setPosts(posts.filter((post) => post._id !== id));
        isSuccess = true;
      } catch {
        isSuccess = false;
      }
      return isSuccess;
    },
    [deletePostMutation, setPosts, posts],
  );
  const offset = useMemo(() => posts.length || 0, [posts]);
  const refetchPosts = useCallback(() => {
    if (homeNoumUser) {
      refetchUserPosts({
        uid: homeNoumUser?._id,
      });
      setNetworkStatus(networkStatusUsersPosts);
    } else {
      refetch({
        chamberId: spaceId,
        filter: filter.filter,
        limit: offset > PAGE_SIZE ? offset : PAGE_SIZE,
        offset: 0,
        sort: filter.sort || {
          column: 'createdAt',
          operator: SortOperator.Desc,
        },
      });
      setNetworkStatus(networkStatusPosts);
      refetchAuthors();
    }
  }, [
    homeNoumUser,
    refetchUserPosts,
    networkStatusUsersPosts,
    refetch,
    spaceId,
    filter.filter,
    filter.sort,
    offset,
    networkStatusPosts,
    refetchAuthors,
  ]);

  const fetchMore = useEvent(async () => {
    if (homeNoumUser) {
      const res = await fetchMoreUserPosts({
        variables: {
          limit: PAGE_SIZE,
          offset,
        },
      });
      const updatedPosts =
        cleanList([...(posts || []), ...(res?.data?.userPosts?.data || [])]) ||
        [];
      setPosts(updatedPosts);
      setTotalCount(res?.data?.userPosts?.count || 0);
      setNetworkStatus(networkStatusUsersPosts);
    } else {
      fetchMorePosts({
        variables: {
          limit: PAGE_SIZE,
          offset,
          filter: filter.filter,
          sort: filter.sort,
        },
      });
      setNetworkStatus(networkStatusPosts);
    }
  });

  useEffect(() => {
    if (
      data?.getPostsByChamberId?.data &&
      !isEqual(data?.getPostsByChamberId?.data, posts)
    ) {
      setPosts(cleanList(data?.getPostsByChamberId?.data || []));
      setTotalCount(data?.getPostsByChamberId?.count || 0);
    }
  }, [
    data?.getPostsByChamberId?.count,
    data?.getPostsByChamberId?.data,
    posts,
  ]);

  useEffect(() => {
    if (homeNoumUser && userPosts?.data?.length) {
      setPosts(cleanList(userPosts?.data) || []);
      setTotalCount(userPosts?.count || 0);
    }
  }, [homeNoumUser, isSpaceOwner, userPosts?.count, userPosts?.data]);

  useEffect(() => {
    setIsCollapseDisabled(posts.length < 1);
  }, [posts]);

  useEffect(() => {
    refetchUserPosts();
    if (!skipToFetchNoumPosts) refetch();
  }, [refetch, refetchUserPosts, skipToFetchNoumPosts]);

  const [authorfilterKeyword, setAuthorFilterKeyword] = useState<string>('');
  const debouncedKeyword = useDebounce<string>(authorfilterKeyword, 500);

  const searchedAuthors = useMemo(() => {
    const list = authors.filter((author) => {
      const fullSearchableWord = trimAndLowerString(
        `${author.firstName || ''}${author.middleName || ''}${
          author.lastName || ''
        }`,
      );
      return fullSearchableWord.includes(trimAndLowerString(debouncedKeyword));
    });
    return list.slice(0, 20);
  }, [authors, debouncedKeyword]);

  const authorFilterOptions = useMemo(() => {
    const options: DropdownValueType<UserOutput | string>[] =
      searchedAuthors.map((item) => ({
        type: 'value',
        value: item,
        key: item._id,
        label: getFullName(item.firstName, item.middleName, item.lastName),
        icon: (
          <Avatar url={item.profile?.profilePictureThumbnail || ''} size="M" />
        ),
      }));

    if (!noumEditor2) {
      return [allOption, ...options];
    }

    return options;
  }, [noumEditor2, searchedAuthors]);

  const value = useMemo(
    () => ({
      loading: loading || loadingPosts || loadingAuthors || loadingUsersPosts,
      isDeleting,
      setLoading,
      isCollapse,
      isCollapseDisabled,
      setIsCollapse,
      noumEditor2,
      posts,
      fetchMore,
      showCreate,
      setShowCreate,
      filter,
      setFilter,
      showFilter,
      setShowFilter,
      deletePost,
      authors,
      refetchPosts,
      setAllPostsLoading,
      isSpaceOwner,
      space,
      isMasterNoum: space?.type === SpaceTypeEnum.Home,
      networkStatus,
      totalCount,
      isCustomPreview,
      allPostsLoading:
        allPostsLoading || loadingPosts || loadingAuthors || loadingUsersPosts,
      authorfilterKeyword,
      setAuthorFilterKeyword,
      authorFilterOptions,
    }),
    [
      loading,
      loadingPosts,
      loadingAuthors,
      loadingUsersPosts,
      isDeleting,
      isCollapse,
      isCollapseDisabled,
      noumEditor2,
      posts,
      fetchMore,
      showCreate,
      filter,
      showFilter,
      deletePost,
      authors,
      refetchPosts,
      isSpaceOwner,
      space,
      networkStatus,
      totalCount,
      isCustomPreview,
      allPostsLoading,
      authorfilterKeyword,
      setAuthorFilterKeyword,
      authorFilterOptions,
    ],
  );

  return (
    <PostElementContext.Provider value={value}>
      {children}
    </PostElementContext.Provider>
  );
};

export const usePostElement = () => useContext(PostElementContext);
