import { useCallback, useEffect, useMemo, useState } from 'react';
import { t } from 'i18next';
import { ActionType, ReactionCategory } from '@/apollo/generated/types';
import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { useToast } from '@/hooks';
import { Spacer } from '@/layout';
import { UserUtil } from '@/utils/user';
import { trackEvent } from '@/utils/tracking';
import {
  useAddReactionMutation,
  useRemoveReactionMutation,
} from '@/apollo/graphql';
import { usePermissions } from '@/features/posts/hooks';
import { ButtonWrap, ItemFooter } from './styles';
import { usePostElement } from '../../PostElementProvider';
import { type PostItemFooterProps } from './types';

const PostItemFooter = (props: PostItemFooterProps) => {
  const {
    data,
    userId,
    userType,
    isCommentsVisible,
    commentsCount,
    handleComment,
    handleThumbUp,
  } = props;
  const { addToast } = useToast();
  const [addReactionMutation] = useAddReactionMutation();
  const [removeReactionMutation] = useRemoveReactionMutation();
  const {
    isSpaceOwner: isOwner,
    noumEditor2,
    isCustomPreview,
  } = usePostElement();
  const isPending = userType === ActionType.Pending;
  const getUserPermission = usePermissions();
  const isPermitted = useMemo(
    () =>
      getUserPermission('POST', 'COMMENT', isOwner ? 'OWNER' : userType) ||
      UserUtil.isInactive(data.uid),
    [data.uid, getUserPermission, isOwner, userType],
  );
  const getLike = useCallback(
    () =>
      data.userReaction
        ? data.userReaction === ReactionCategory.Like
        : data?.reactions?.find?.((r) => r?.uid?._id === (userId || '')),
    [data?.reactions, data.userReaction, userId],
  );
  const [like, setLike] = useState(getLike());
  const [likeClicked, setLikeClicked] = useState(false);

  useEffect(() => setLike(getLike()), [getLike]);

  const manageInActiveClickEvent = useCallback(() => {
    setLikeClicked(true);
    const clickTimeout = setTimeout(() => {
      setLikeClicked(false);
      clearInterval(clickTimeout);
    }, 3600);
  }, [setLikeClicked]);

  const toastOnLike = useCallback(() => {
    addToast('primary', 'none', t('noumena.community.userIsNotActive.like'));
    manageInActiveClickEvent();
  }, [addToast, manageInActiveClickEvent]);

  const reactionMutation = like ? removeReactionMutation : addReactionMutation;
  const handleLike = useCallback(async () => {
    setLikeClicked(true);
    setLike((prevState) => !prevState);
    handleThumbUp?.(like ? 'UnLike' : 'LIKE');
    await reactionMutation({
      variables: {
        _id: data._id,
        type: ReactionCategory.Like,
      },
    });
    if (like)
      trackEvent('liked_post', {
        DeviceType: navigator.userAgent,
        PostId: data._id,
      });
    setLikeClicked(false);
  }, [handleThumbUp, like, reactionMutation, data._id]);

  const likeColor = like
    ? '--icon-card-brand-primary-default'
    : '--icon-card-neutral-default';
  const buttonColor = like
    ? '--button-card-brand-primary-default'
    : '--button-card-neutral-default';
  const commentIconColor = isCommentsVisible
    ? '--icon-card-brand-primary-default'
    : '--icon-card-neutral-default';
  const commentButtonColor = isCommentsVisible
    ? '--button-card-brand-primary-default'
    : '--button-card-neutral-default';

  return (
    <ItemFooter noumEditor2={noumEditor2 && !isCustomPreview}>
      <ButtonWrap
        onClick={isPending ? toastOnLike : handleLike}
        disable={likeClicked || !isPermitted}
      >
        <Icon name="thumb_up_m" size={24} color={likeColor} />
        <Spacer width={10} />
        <TSpan font="body-m-bold" colorToken={buttonColor}>
          {like ? t('noumena.reaction.liked') : t('noumena.reaction.like')}
        </TSpan>
      </ButtonWrap>
      <Spacer width={24} />
      <ButtonWrap onClick={handleComment} disable={!isPermitted}>
        <Icon name="message_m" size={24} color={commentIconColor} />
        <Spacer width={10} />
        <TSpan font="body-m-bold" colorToken={commentButtonColor}>
          {commentsCount || t('noumena.reaction.comment')}
        </TSpan>
      </ButtonWrap>
    </ItemFooter>
  );
};

export default PostItemFooter;
