import { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  CommentInputContainer,
  SendButton,
  SpinnerContainer,
} from '@/screens/Chamber/components/elements/Comments/styles';
import { Icon } from '@/components/Icon';
import { ICON_SIZE } from '@/screens/Chamber/components/elements/PostElement/components/PostItem/constants';
import { type CommentItemAddProps } from '@/screens/Chamber/components/elements/Comments/CommentItem/types';
import { Spinner } from '@/components/Spinner';
import { Mention } from '@/features/posts/components';
import { COMMENT_MAX_LEN } from '../constants';

const CommentItemAdd = ({
  setContent,
  createComment = () => {},
  posting = false,
  addTag = () => {},
  isReply,
}: CommentItemAddProps) => {
  const [value, setValue] = useState<string>('');
  const { t } = useTranslation();
  const isError = useMemo(
    () => value.length > COMMENT_MAX_LEN || !value.trim(),
    [value],
  );

  const setCommentValue = useCallback(
    (content) => {
      setValue(content.slice(0, COMMENT_MAX_LEN));
      setContent(content.slice(0, COMMENT_MAX_LEN));
    },
    [setContent],
  );

  return (
    <CommentInputContainer>
      <div style={{ width: 0, flex: 1 }}>
        <Mention
          val={value}
          setVal={setCommentValue}
          addTag={addTag}
          error={false}
          isTextArea
          textAreaHeight={64}
          maxLength={COMMENT_MAX_LEN}
          placeholder={isReply ? t('noumena.reply.placeholder') : undefined}
        />
      </div>
      {posting ? (
        <SpinnerContainer>
          <Spinner key="Spinner" />
        </SpinnerContainer>
      ) : (
        <SendButton
          data-testid="comment-item-add-send"
          neutral
          disabled={isError}
          icon={
            <Icon
              color={
                isError
                  ? '--icon-button-neutral-disabled'
                  : '--icon-button-neutral-pressed'
              }
              name="send_m"
              size={ICON_SIZE}
            />
          }
          onClick={createComment}
        />
      )}
    </CommentInputContainer>
  );
};

export default CommentItemAdd;
