import { t } from 'i18next';
import { useCallback, useMemo } from 'react';
import { capitalize } from 'lodash';
import { NavLink, useLocation } from 'react-router-dom';
import { Avatar } from '@/components/Avatar/Avatar';
import { PinDotsItem } from '@/features/posts/components';
// import { TimeRelative } from '@/components/TimeRelative';
import { TSpan } from '@/components/Typography';
import { getFullName } from '@/utils/fullName';
import { UserUtil } from '@/utils/user';
import { PinTab, PinTabText } from '@/screens/Community/AllPosts/styles';
import { Icon } from '@/components/Icon';
import { PostVisibility } from '@/apollo/generated/types';
import { useToast } from '@/hooks';
import { useSkeletonIsLoadingContext } from '@/components/SkeletonLoader/SkeletonLoaderProvider';
import { CustomPreviewTabEnum } from '@/screens/Chamber/CustomPreview/constants';
import { type PostItemHeadProps } from './types';
import { usePostElement } from '../../PostElementProvider';
import {
  AvatarContainer,
  ItemHead,
  NameWrap,
  TimeWrap,
  PostDetailsWrapper,
} from './styles';

const PostItemHead = (props: PostItemHeadProps) => {
  const {
    data,
    options,
    userType,
    onHandleSelect,
    isPinned = false,
    selectedCustomPreviewTab,
    size = 'M',
    isPostAuthor,
  } = props;
  const {
    isSpaceOwner: isOwner,
    noumEditor2,
    isCustomPreview,
  } = usePostElement();
  const { isLoading } = useSkeletonIsLoadingContext();
  const { pathname } = useLocation();
  const fullName = useMemo(
    () =>
      getFullName(
        data.uid?.firstName,
        data.uid?.middleName,
        data.uid?.lastName,
      ),
    [data.uid],
  );

  const isUnregisteredUser = useMemo(
    () => UserUtil.isUnregistered(data.uid),
    [data.uid],
  );

  const isSinglePostPage = useMemo(
    () => pathname.startsWith('/post/') || pathname.startsWith('/posts/'),
    [pathname],
  );
  const isCommunityPage = useMemo(
    () => pathname.startsWith('/community'),
    [pathname],
  );

  const isInactive = useMemo(() => UserUtil.isInactive(data.uid), [data.uid]);
  const isNonClickable = useMemo(
    () => isLoading || isInactive || isUnregisteredUser,
    [isInactive, isLoading, isUnregisteredUser],
  );

  const navLinkTo = useMemo(
    () => (isNonClickable ? '' : `/noum/${data?.uid?.chamber?._id}`),
    [data?.uid?.chamber?._id, isNonClickable],
  );

  const navLinkStyle = useMemo(
    () => ({
      textDecoration: 'none',
      cursor: isNonClickable ? 'default' : 'pointer',
    }),
    [isNonClickable],
  );

  const visibilityTitle = useMemo(
    () =>
      data?.visibility === PostVisibility.All
        ? ''
        : data?.visibility === PostVisibility.Follower
        ? `  ${t('noumena.chambers.element.posts.visibility_followers')}`
        : `  ${capitalize(data.visibility || '')}`,
    [data.visibility],
  );
  const { addPrimaryIconToast } = useToast();

  const handleClick = useCallback(() => {
    if (isInactive || isUnregisteredUser)
      addPrimaryIconToast(
        t('noumena.calendar.notification.button.inactive_user'),
      );
  }, [addPrimaryIconToast, isInactive, isUnregisteredUser]);

  return (
    <ItemHead noumEditor2={noumEditor2 && !isCustomPreview}>
      <NavLink to={navLinkTo} style={navLinkStyle} onClick={handleClick}>
        <AvatarContainer>
          <Avatar
            url={UserUtil.getProfilePicture(data.uid) ?? ''}
            size={!isCommunityPage && noumEditor2 ? 'L' : size}
          />
        </AvatarContainer>
      </NavLink>

      <NameWrap>
        <PostDetailsWrapper>
          <NavLink to={navLinkTo} style={navLinkStyle} onClick={handleClick}>
            <TSpan
              font="body-l-bold"
              colorToken="--text-card-header-neutral-highlighted"
            >
              {isInactive ? t('noumena.inactive_user') : fullName}
            </TSpan>
          </NavLink>
          {isSinglePostPage && data?.chamber && (
            <>
              <Icon
                name="arrow_right_m"
                size={24}
                color="--icon-button-neutral-default"
              />
              <NavLink
                to={`/noum/${data.chamber?._id}`}
                style={navLinkStyle}
                onClick={handleClick}
              >
                <TSpan
                  font="body-l-bold"
                  colorToken="--text-card-header-neutral-highlighted"
                >
                  {data.chamber?.name}
                </TSpan>
              </NavLink>
            </>
          )}
        </PostDetailsWrapper>

        <TimeWrap>
          {isUnregisteredUser && (
            <TSpan
              font="body-s"
              colorToken="--text-timestamp-neutral-highlighted"
            >
              {`${t('noumena.chamber_edit.modal.non_member')}  `}
            </TSpan>
          )}
          {/* // TODO: temporary change for NOUM-5913 */}
          {/* <TimeRelative
            font="body-s"
            colorToken="--text-timestamp-neutral-default"
            date={new Date(data.createdAt)}
          /> */}
          <TSpan
            font="body-s"
            colorToken="--text-timestamp-neutral-highlighted"
          >
            {visibilityTitle}
          </TSpan>
          {isPinned && (
            <PinTab>
              <Icon
                name="pin_m"
                size={16}
                color="--icon-tag-neutral-alt-default"
                style={{ maxHeight: '100%' }}
                isPinned
              />
              <PinTabText>{t('noumena.post.pinned_post')}</PinTabText>
            </PinTab>
          )}
        </TimeWrap>
      </NameWrap>
      {selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
        <PinDotsItem
          isPostAuthor={isPostAuthor}
          isOwner={isOwner}
          isInactive={isInactive}
          userType={userType}
          onHandleSelect={onHandleSelect}
          options={options}
        />
      )}
    </ItemHead>
  );
};

export default PostItemHead;
