import {
  useCallback,
  useEffect,
  useLayoutEffect,
  useMemo,
  useState,
} from 'react';
import { type CommentsProps } from '@/screens/Chamber/components/elements/Comments/types';
import CommentItem from '@/screens/Chamber/components/elements/Comments/CommentItem';
import useEvent from '@/hooks/useEvent';
import {
  type GetRepliesByCommentIdQuery,
  type PostCommentsQuery,
  useAddCommentToPostMutation,
  useAddReplyToCommentMutation,
  useDeleteReplyToCommentMutation,
  useGetRepliesByCommentIdQuery,
  usePostCommentsQuery,
  useRemoveCommentMutation,
} from '@/apollo/graphql';
import CommentItemAdd from '@/screens/Chamber/components/elements/Comments/CommentItem/ComentItemAdd';
import LoadMore from '@/screens/Chamber/components/elements/Comments/LoadMore';
import {
  COMMENT_MAX_LEN,
  COMMENTS_LIMIT,
} from '@/screens/Chamber/components/elements/Comments/constants';
import { trackEvent } from '@/utils/tracking';
import { useAuth } from '@/features/auth/contexts';
import { Spinner } from '@/components/Spinner';
import {
  ActionType,
  type Comments,
  type ReplyOutput,
  SortOperator,
  type TagsInput,
} from '@/apollo/generated/types';
import {
  CommentsContainer,
  CommentsListContainer,
  SpinnerContainer,
} from './styles';
import { isValidPostId } from '../PostElement/helpers';

const CommentsList = ({
  isPostOwner,
  id,
  isReply = false,
  replies,
  showInput = false,
  handleCreate,
  handleDelete,
  isReplies,
  setIsReplies,
  parentId,
  level,
  isNoumEditor2CommentView,
}: CommentsProps) => {
  const { user } = useAuth();
  const { userStatus } = user || {};
  const listFetcher = useMemo(
    () => (isReply ? useGetRepliesByCommentIdQuery : usePostCommentsQuery),
    [isReply],
  );

  const [addCommentMutation] = useAddCommentToPostMutation();
  const [addReplayMutation] = useAddReplyToCommentMutation();
  const createReply = isReply ? addReplayMutation : addCommentMutation;
  const variables = useMemo(
    () => ({
      limit: isReply ? 1 : COMMENTS_LIMIT,
      offset: 0,
      postId: id,
      commentId: id,
    }),
    [id, isReply],
  );
  const { data, loading, fetchMore, refetch } = listFetcher({
    variables,
    fetchPolicy: 'cache-and-network',
    skip: !isValidPostId(id),
  });
  const listIndex = useMemo(
    () => (isReply ? 'getRepliesByCommentId' : 'postComments'),
    [isReply],
  );
  const getListFromQuery = useCallback(
    (d: PostCommentsQuery | GetRepliesByCommentIdQuery | undefined) =>
      // @ts-ignore
      (d?.[listIndex]?.data || []) as unknown as Comments[],
    [listIndex],
  );
  const [list, setList] = useState([]);
  const [posting, setPosting] = useState(false);
  // @ts-ignore
  const count = useMemo(() => data?.[listIndex]?.count || 0, [data, listIndex]);
  const [isReplyInputVisible, setIsReplyInputVisible] = useState(
    userStatus !== ActionType.Pending,
  );
  const [content, setContent] = useState<string>('');
  const [tags, setTags] = useState<TagsInput[]>([]);

  const addComment = useEvent(async ({ _id }) => {
    setPosting(true);
    createReply({
      variables: { _id, commentId: _id, content: content.trim(), tags },
    })
      .then(async () => {
        const result = await refetch();
        handleCreate?.();
        if (level === 0) {
          // @ts-ignore
          setList([getListFromQuery(result?.data)?.[0], ...list]);
        } else if (level === 1) {
          // @ts-ignore
          setList([...list, getListFromQuery(result?.data)?.[0]]);
        }
        setIsReplyInputVisible(false);
        setTimeout(
          () => setIsReplyInputVisible(userStatus !== ActionType.Pending),
          0,
        );
      })
      .finally(() => setPosting(false));
  });
  const createNewComment = useEvent(() => {
    if (content) {
      addComment({
        _id: id,
        id,
        content: content.slice(0, COMMENT_MAX_LEN),
        tags,
      });
      trackEvent('comment_on_post', {
        DeviceType: navigator.userAgent,
        UUID: user?._id,
        PostId: id,
      });
    }
  });

  const handleLoadMore = async () => {
    let sortOrder = SortOperator.Desc;
    let offset = list.length;
    if (level === 1) {
      sortOrder = SortOperator.Asc;
      offset = 0;
    }

    const { data: moreList } = await fetchMore({
      variables: {
        ...variables,
        sortOrder,
        offset,
        limit: undefined,
      },
    });

    if (level === 0) {
      // @ts-ignore
      setList((prev) => [...prev, ...(moreList?.[listIndex]?.data || [])]);
    } else if (level === 1) {
      // @ts-ignore
      setList([...(moreList?.[listIndex]?.data || [])]);
    }
  };

  const addTag = useCallback(
    (uid: string | number) => {
      const newTagsArray = [...tags, { uid }] as TagsInput[];
      setTags(newTagsArray);
    },
    [setTags, tags],
  );

  useLayoutEffect(
    // @ts-ignore
    () => setList(getListFromQuery(data)),
    [data, loading, getListFromQuery],
  );
  const [removeCommentMutation] = useRemoveCommentMutation({});

  const deleteComment = useCallback(
    (postId, commentId) => {
      removeCommentMutation({
        variables: {
          _id: postId,
          commentId,
        },
      })
        .then(() => {
          refetch();
        })
        .catch(() => {
          refetch();
        });
    },
    [refetch, removeCommentMutation],
  );

  useEffect(() => {
    if (isReplies !== Boolean(list.length))
      setIsReplies?.(Boolean(list.length));
  }, [isReplies, list, setIsReplies]);

  const [deleteReplyToComment] = useDeleteReplyToCommentMutation();
  const deleteReply = useCallback(
    (commentId: string, postId: string, replyId: string) => {
      deleteReplyToComment({
        variables: {
          commentId,
          postId,
          replyId,
        },
      })
        .then(() => {
          refetch();
        })
        .catch(() => {
          refetch();
        });
    },
    [refetch, deleteReplyToComment],
  );

  return (
    <CommentsListContainer
      hasChild={list.length > 0}
      isNoumEditor2CommentView={isNoumEditor2CommentView}
    >
      {isReplyInputVisible && !isReply && (
        <CommentItemAdd
          setContent={setContent}
          createComment={createNewComment}
          isReply={isReply}
          posting={posting}
          addTag={addTag}
        />
      )}
      {loading && (
        <SpinnerContainer>
          <Spinner key="Spinner" />
        </SpinnerContainer>
      )}
      {list?.length < count && (
        <LoadMore
          handleLoadMore={handleLoadMore}
          isReplies={isReply}
          replies={
            replies ||
            ((data as PostCommentsQuery)?.postComments?.data as Comments[]) ||
            ([] as ReplyOutput)
          }
          count={count - list.length}
        />
      )}
      {list && (
        <CommentsContainer>
          {list?.map((comment: Comments, index: number) =>
            comment ? (
              <CommentItem
                deleteComment={(postId, commentId) => {
                  deleteComment(postId, commentId);
                  handleDelete?.();
                }}
                deleteReply={() => {
                  deleteReply(id, parentId || '', comment?._id);
                }}
                isPostOwner={isPostOwner}
                postId={id}
                isReply={isReply}
                key={comment._id}
                item={comment}
                isLastItem={index === list.length - 1}
                isNoumEditor2CommentView={isNoumEditor2CommentView}
              />
            ) : null,
          )}
        </CommentsContainer>
      )}
      {isReplyInputVisible && isReply && showInput && (
        <CommentItemAdd
          setContent={setContent}
          createComment={createNewComment}
          isReply={isReply}
          posting={posting}
          addTag={addTag}
        />
      )}
    </CommentsListContainer>
  );
};

export default CommentsList;
