import { t } from 'i18next';
import generate from 'uniqid';
import { TSpan } from '@/components/Typography';
import { Stack } from '@/layout';
import { Dropdown } from '@/components/Dropdown';
import { TextField } from '@/components/TextField';
import { Icon } from '@/components/Icon';
import { Mention } from '@/features/posts/components';
import { useLaunchDarkly, useWindowDimensions } from '@/hooks';
import { breakpoints } from '@/constants/devices';
import { ModalBody } from '@/components/ExtendedModal';
import { type CreatePostBodyProps } from './types';
import { AssetItem } from './AssetItem';
import {
  EditorContainer,
  ImageContainers,
  StyledTextAreaContainer,
  VisibilitySelect,
} from './styles';
import PostModalUserInfo from './PostModalUserInfo';
import PostRichTextEditor from '../../components/PostRichTextEditor';

const PostModalBody = (props: CreatePostBodyProps) => {
  const {
    images,
    showVisibility,
    options,
    postVisibility,
    setPostVisibility,
    text,
    setText,
    addTag,
    categories,
    handleDeleteAssetItem,
    overflow,
    onClipboard,
    post,
    setRawJson,
  } = props;
  const {
    flags: { postRte },
  } = useLaunchDarkly();
  const { width } = useWindowDimensions();
  const isMobile = width < breakpoints.TABLET;

  return (
    <ModalBody overflow={overflow}>
      <EditorContainer
        fullWidth
        vertical
        data-testid="post_editor_container"
        isRTE={postRte}
      >
        <Stack
          fullWidth
          justify="space-between"
          align="center"
          style={{ marginBottom: '16px', gap: '16px' }}
        >
          <PostModalUserInfo />
          {!isMobile && showVisibility && (
            <VisibilitySelect data-testid="post_visibility_selector">
              <TSpan font="footnote" colorToken="--text-input-neutral-default">
                {t('noumena.chambers.element.posts.visible_for')}
              </TSpan>
              <Dropdown
                hideIcons
                placement="bottom-start"
                options={options}
                inputValue={postVisibility?.value || undefined}
                onSelectOption={(option) => setPostVisibility(option)}
                usePortal={false}
                containerWidth="211px"
                minHeight="100px"
              >
                {({ inputProps, inputRef, toggle }) => (
                  <TextField
                    readOnly
                    {...inputProps}
                    ref={inputRef}
                    value={String(postVisibility.label)}
                    spellCheck="false"
                    rightIcon={
                      <Icon
                        name="chevron_down_m"
                        color="--icon-input-neutral-default"
                        size={16}
                        onClick={toggle}
                      />
                    }
                  />
                )}
              </Dropdown>
            </VisibilitySelect>
          )}
        </Stack>
        <StyledTextAreaContainer>
          {postRte ? (
            <PostRichTextEditor
              initialData={post}
              setEditorValue={setText}
              setEditorRawValue={setRawJson}
              isEdit={true}
            />
          ) : (
            <Mention
              val={text}
              setVal={setText}
              addTag={addTag}
              isTextArea
              isFullHeight
              autoFocus
              placeholder={t('noumena.chambers.element.posts.text_placeholder')}
              error={false}
              textAreaHeight={300}
              onClipboard={onClipboard}
            />
          )}
        </StyledTextAreaContainer>
        {images.length > 0 && (
          <ImageContainers>
            {images.map((image, idx) => (
              <AssetItem
                index={idx}
                key={generate()}
                url={image}
                filetype={categories[idx]}
                onDelete={handleDeleteAssetItem}
              />
            ))}
          </ImageContainers>
        )}
      </EditorContainer>
    </ModalBody>
  );
};

export default PostModalBody;
