import { useCallback, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import generate from 'uniqid';
import { InlineAvatar } from '@/components/Avatar/Inline/Inline';
import { DeletePost } from '@/screens/Chamber/components/elements/PostElement/modals/DeletePost';
import { getFileExtension, getVideoMimeType } from '@/utils/file';
import OwnerDefaultImage from '@/assets/images/profile_default.png';
import {
  ActionType,
  PostCategory,
  PostVisibility,
  type Reaction,
} from '@/apollo/generated/types';
import { TSpan } from '@/components/Typography';
import { type DropdownValueType } from '@/components/Dropdown';
import CommentsList from '@/screens/Chamber/components/elements/Comments';
import { useAuth } from '@/features/auth/contexts';
import { useError, useLaunchDarkly, useToast } from '@/hooks';
import {
  useDeletePostMutation,
  usePinPostMutation,
  usePostCommentsLazyQuery,
} from '@/apollo/graphql';
import useEvent from '@/hooks/useEvent';
import {
  usePermissions,
  useGetUserType,
  type UserType,
} from '@/features/posts/hooks';
import VideoPlayer from '@/components/VideoPlayer';
import LightBox from '@/components/LightBox';
import { UserUtil } from '@/utils/user';
import { ViewType } from '@/components/LightBox/types';
import { type Maybe } from '@/common/types';
import { CustomPreviewTabEnum } from '@/screens/Chamber/CustomPreview/constants';
import ParsedContent from '@/screens/Community/ParsedContent';
import { matchPath, useLocation, useNavigate } from 'react-router';
import ROUTES from '@/constants/routes';
import CreatePost from '../../modals/CreatePost';
import { defaultPostActionOptions, pinningOptions } from './data';
import PostItemHead from './PostItemHead';
import PostItemFooter from './PostItemFooter';
import PostRichTextEditor from '../PostRichTextEditor';
import { ReportPostModal } from '../../modals/ReportPostModal';
import { usePostElement } from '../../PostElementProvider';
import {
  Container,
  Image,
  ItemImageWrap,
  ItemReaction,
  RecentTitle,
  ShowMore,
  TextWrapper,
} from './styles';
import { type PostActionType, type PostItemProps } from './types';
import { isValidPostId } from '../../helpers';

const PostItem = (props: PostItemProps) => {
  const {
    data,
    collapsed,
    recent,
    refetch = () => {},
    isPinningEnabled = false,
    isMasterNoum,
    isMarginTop = false,
    isCommunity = false,
    selectedCustomPreviewTab,
    forceRender,
    setForceRender,
    pageView,
    size,
  } = props;
  const {
    flags: { postRte },
  } = useLaunchDarkly();
  const { isPinned = false } = data;
  const { t } = useTranslation();
  const { addToast, addSuccessIconToast } = useToast();
  const [postAction, setPostAction] = useState<PostActionType>(undefined);
  const [commentsCount, setCommentsCount] = useState<number>(
    data?.commentsCount || 0,
  );
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const {
    refetchPosts,
    isSpaceOwner: isOwner,
    space,
    noumEditor2,
    isCustomPreview,
    deletePost: deleteSinglePost,
    isDeleting,
  } = usePostElement();
  const [deleteCommunityPost, { loading: isDeletingCommunity }] =
    useDeletePostMutation();

  const [pinUnpinPost] = usePinPostMutation();
  const { user } = useAuth();
  const isPostAuthor = data.uid?._id === user?._id;
  const { logError } = useError();

  const [showMore, setShowMore] = useState<boolean | undefined>(collapsed);
  const [preview, setPreview] = useState(false);
  const [lightBoxUrl, setLightBoxUrl] = useState('');
  const [reactions, setReactions] = useState<
    Maybe<Maybe<Reaction>[]> | undefined
  >(data?.reactions);

  const getUserType = useGetUserType();
  const getUserPermission = usePermissions();

  const pinningOption = isPinned ? pinningOptions[1] : pinningOptions[0];

  const options = isPinningEnabled
    ? [pinningOption, ...defaultPostActionOptions]
    : defaultPostActionOptions;
  const [getCommentCount] = usePostCommentsLazyQuery({
    fetchPolicy: 'cache-and-network',
    onCompleted: (response) => {
      if (response.postComments) {
        setCommentsCount(response.postComments?.count ?? 0);
      }
    },
  });

  const isNoumPage = matchPath({ path: ROUTES.NOUM }, pathname);
  const isHomePage = matchPath({ path: ROUTES.HOME_NOUM }, pathname);
  const isAllPostsPage = matchPath({ path: ROUTES.POSTS }, pathname);
  const isSinglePostPage = matchPath({ path: ROUTES.POST }, pathname);
  const isCommunityPage = matchPath({ path: ROUTES.COMMUNITY }, pathname);

  const [commentClicked, setCommentClicked] = useState(false);

  const manageInActiveClickEvent = useCallback(() => {
    setCommentClicked(true);
    const clickTimeout = setTimeout(() => {
      setCommentClicked(false);
      clearInterval(clickTimeout);
    }, 3600);
  }, [setCommentClicked]);

  const userType: UserType = useMemo(() => {
    if (user?.userStatus === ActionType.Pending) return ActionType.Pending;
    let result: UserType = 'GUEST';
    const owner = data?.uid?._id === user?._id;
    if (user?.roles) {
      const admin = user.roles.find((role) => role?.roleType === 'ADMIN');
      if (owner) result = 'OWNER';
      if (admin) result = 'ADMIN';
      if (owner && admin) result = 'ADMINOWNER';
      if (result !== 'GUEST') return result;
    }
    return isOwner || owner
      ? 'OWNER'
      : getUserType({
          feature: 'POST',
          visibility: data.visibility || PostVisibility.Follower,
          isConnected: space?.isConnected ?? false,
          isFollowing: space?.isFollowing ?? false,
        });
  }, [isOwner, data, space, user, getUserType]);

  const [isCommentsVisible, setIsCommentsVisible] = useState(pageView ?? false);
  const handleComment = useEvent(() => {
    if (isNoumPage && noumEditor2 && !isCustomPreview) {
      navigate(`/post/${data._id}?noumId=${data.chamberId}`);
    } else if (isHomePage && noumEditor2) {
      navigate(`/post/${data._id}`);
    } else {
      if (UserUtil.isInactive(data.uid)) return;
      if (!getUserPermission('POST', 'COMMENT', isOwner ? 'OWNER' : userType))
        return;
      if (!isCommentsVisible && userType === ActionType.Pending) {
        if (commentClicked) return;
        addToast(
          'primary',
          'none',
          t(`noumena.community.userIsNotActive.comment`),
        );
        manageInActiveClickEvent();
      }
      setIsCommentsVisible((pre) => {
        if (noumEditor2 && !isCustomPreview) {
          return !pre;
        }
        return (
          pageView ??
          (!isCommentsVisible &&
            !(userType === ActionType.Pending && !commentsCount))
        );
      });
    }
  });

  const urls = useMemo<(string | undefined)[]>(
    () =>
      (reactions || [])
        .map(
          (reaction) =>
            UserUtil.getProfilePicture(reaction?.uid) || OwnerDefaultImage,
        )
        .slice(0, 3),
    [reactions],
  );

  const refreshPosts = useCallback(async () => {
    await refetch?.();
    await refetchPosts?.();
  }, [refetch, refetchPosts]);

  const deletePost = useCallback(
    async (id) => {
      if (isCommunityPage) {
        try {
          await deleteCommunityPost({
            variables: {
              _id: id,
            },
          });
          refreshPosts();
        } catch (err) {
          logError(err, '', false);
          return;
        }
        return;
      }
      const isSuccess = await deleteSinglePost(id);
      refreshPosts();
      if (isSuccess && isSinglePostPage && noumEditor2) navigate(-1);
    },
    [
      deleteCommunityPost,
      deleteSinglePost,
      isCommunityPage,
      isSinglePostPage,
      logError,
      navigate,
      noumEditor2,
      refreshPosts,
    ],
  );

  const onHandleSelect = (option: DropdownValueType<string>) => {
    setPostAction(option.value as PostActionType);
  };

  const handleDelete = async () => {
    await deletePost(data._id);
  };

  const images = useMemo(() => {
    if (
      typeof data.post?.content === 'string' ||
      typeof data.post?.content === 'object'
    ) {
      return [{ ...data.post, key: generate() }];
    }
    return [];
  }, [data]);

  const handlePinUnpin = useCallback(async () => {
    setPostAction(undefined);
    await pinUnpinPost({
      variables: {
        postId: data._id,
      },
    })
      .then(async ({ data: postData }) => {
        const pinned = postData?.pinPost?.isPinned;
        addSuccessIconToast(
          t(
            `noumena.chambers.element.posts.success.${
              pinned ? 'pin' : 'unpin'
            }`,
          ),
        );
        setForceRender?.(!forceRender);
      })
      .catch(() => {
        addToast(
          'error',
          'none',
          t(
            `noumena.chambers.element.posts.error.${
              isPinned ? 'pin' : 'unpin'
            }_failed`,
          ),
        );
      });
  }, [
    pinUnpinPost,
    data._id,
    addSuccessIconToast,
    t,
    setForceRender,
    forceRender,
    addToast,
    isPinned,
  ]);

  const handleLike = useEvent((actionName: string) =>
    actionName === 'LIKE'
      ? // @ts-ignore
        setReactions((prev) => [...prev, { uid: user }])
      : setReactions((prev) => prev?.filter((r) => r?.uid?._id !== user?._id)),
  );
  const handleCommentCreate = useCallback(() => {
    if (isValidPostId(data._id)) {
      getCommentCount({
        variables: {
          limit: 1,
          offset: 0,
          postId: data._id,
        },
      });
    }
  }, [data._id, getCommentCount]);

  useEffect(() => {
    if (!postAction) return;
    if (postAction === 'Pin' || postAction === 'Unpin') {
      handlePinUnpin();
    }
  }, [handlePinUnpin, postAction]);
  useEffect(() => setReactions(data?.reactions), [data?.reactions]);

  useEffect(() => {
    handleCommentCreate?.();
  }, [handleCommentCreate]);

  return (
    <Container
      isMarginTop={isMarginTop}
      isPinned={isPinned}
      pageView={pageView}
      noumEditor2={noumEditor2 && !isCustomPreview}
      isCommunity={isCommunity}
      isPostView={!!isAllPostsPage}
    >
      {postAction === 'Delete' && (
        <DeletePost
          postId={data._id}
          isDeleting={isCommunityPage ? isDeletingCommunity : isDeleting}
          onClose={() => setPostAction(undefined)}
          onDelete={handleDelete}
        />
      )}
      {postAction === 'Report' && (
        <ReportPostModal
          key={data._id}
          postId={data._id}
          onClose={() => setPostAction(undefined)}
        />
      )}
      {postAction === 'Edit' && (
        <CreatePost
          post={data}
          onClose={() => setPostAction(undefined)}
          onSuccess={() => {
            setPostAction(undefined);
            refreshPosts();
          }}
          onEdit={() => refreshPosts?.()}
          isMasterNoum={isMasterNoum}
          isCommunity={isCommunity}
        />
      )}
      {recent && (
        <RecentTitle>
          <TSpan font="body-m" colorToken="--text-body-neutral-default">
            {t('noumena.recent_post_title')}
          </TSpan>
        </RecentTitle>
      )}
      <PostItemHead
        data={data}
        isPinned={Boolean(isPinned && !pageView)}
        options={options}
        userType={userType}
        onHandleSelect={onHandleSelect}
        selectedCustomPreviewTab={selectedCustomPreviewTab}
        size={size}
        isPostAuthor={isPostAuthor}
      />
      {data?.text && (
        <>
          <TextWrapper
            isCollapsed={!!(recent && showMore)}
            noumEditor2={noumEditor2 && !isCustomPreview}
          >
            {postRte ? (
              <PostRichTextEditor initialData={data} isEdit={false} />
            ) : (
              <ParsedContent isPost item={data} />
            )}
          </TextWrapper>
          {recent && showMore && (
            <ShowMore
              onClick={() => {
                setShowMore(false);
              }}
              colorToken="--link-card-brand-primary-default"
            >
              {t('noumena.elements.posts.show_more')}
            </ShowMore>
          )}
        </>
      )}

      {images.map((img) => (
        <ItemImageWrap key={img.key}>
          {img.category === PostCategory.Image && (
            <Image
              src={img.content || ''}
              alt="post"
              data-testid="postImage"
              onClick={() => {
                setLightBoxUrl(img.content ?? '');
                setPreview(true);
              }}
              noumEditor2={noumEditor2 && !isCustomPreview}
            />
          )}
          {img.category === PostCategory.Video && (
            <VideoPlayer
              url={img.content}
              fileType={getVideoMimeType(getFileExtension(img?.content ?? ''))}
              controls
            />
          )}
        </ItemImageWrap>
      ))}
      <LightBox
        url={lightBoxUrl}
        type={ViewType.IMAGE}
        isOpen={preview}
        handleClose={() => {
          setPreview(false);
        }}
      />
      {(reactions?.length || 0) > 0 &&
        selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
          <ItemReaction noumEditor2={noumEditor2}>
            <InlineAvatar size="M" borderedImage={true} urls={urls} />
            <TSpan font="body-s" colorToken="--text-card-neutral-default">
              {t('noumena.post.liked_this_post', {
                count: reactions?.length || 0,
              })}
            </TSpan>
          </ItemReaction>
        )}
      {selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
        <PostItemFooter
          data={data}
          userType={userType}
          isCommentsVisible={Boolean(isCommentsVisible && !pageView)}
          commentsCount={commentsCount}
          handleComment={handleComment}
          handleThumbUp={handleLike}
          userId={user?._id}
        />
      )}
      {isCommentsVisible && (
        <CommentsList
          isPostOwner={isOwner}
          id={data._id}
          handleCreate={() => setCommentsCount(commentsCount + 1)}
          handleDelete={() => setCommentsCount(commentsCount - 1)}
          level={0}
          isNoumEditor2CommentView={
            noumEditor2 && (!!isAllPostsPage || !!isSinglePostPage)
          }
        />
      )}
    </Container>
  );
};

export default PostItem;
