/* eslint-disable @typescript-eslint/no-shadow */
import _, { flattenDeep } from 'lodash';
import {
  type Dispatch,
  Fragment,
  type ReactElement,
  type SetStateAction,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  EditorState,
  convertToRaw,
  type EntityInstance,
  convertFromRaw,
  type DraftHandleValue,
  CompositeDecorator,
  type DraftDecorator,
  type RawDraftContentState,
  Modifier,
  ContentState,
} from 'draft-js';
import {
  stateToHTML,
  type Options as OptionsStateToHTML,
} from 'draft-js-export-html';
import {
  stateFromHTML,
  type Options as OptionsStateFromHTML,
} from 'draft-js-import-html';
import Editor from '@draft-js-plugins/editor';
import createMentionPlugin, {
  type MentionData,
} from '@draft-js-plugins/mention';
import createToolbarPlugin, {
  Separator,
} from '@draft-js-plugins/static-toolbar';
import createLinkifyPlugin from '@draft-js-plugins/linkify';
import {
  BoldButton,
  ItalicButton,
  UnorderedListButton,
  OrderedListButton,
  UnderlineButton,
  HeadlineOneButton,
  HeadlineTwoButton,
} from '@draft-js-plugins/buttons';
import { useGlobalSearchLazyQuery } from '@/apollo/graphql';
import { EntityType, type PostOutput } from '@/apollo/generated/types';
import OwnerDefaultImage from '@/assets/images/profile_default.png';
import { returnParsedTagsText } from '@/screens/Community/utils';
import { useBreakpoints } from '@/hooks/useBreakpoints';
import { cleanList } from '@/utils/list';
import { Container, MentionLink, ToolbarContainer } from './styles';
import parseRawWithMentions from './utils';
import 'draft-js/dist/Draft.css';
import '@draft-js-plugins/mention/lib/plugin.css';
import '@draft-js-plugins/static-toolbar/lib/plugin.css';
import mentionStyles from './MentionStyles.module.css';

type PostRichTextEditorProps = {
  initialData?: PostOutput;
  setEditorValue?: Dispatch<SetStateAction<string>>;
  setEditorRawValue?: Dispatch<
    SetStateAction<RawDraftContentState | undefined>
  >;
  isEdit?: boolean;
};

const options: OptionsStateToHTML = {
  entityStyleFn: (entity: EntityInstance) => {
    const entityType = entity.getType().toLowerCase();
    if (entityType === 'mention') {
      const data = entity.getData();
      return {
        element: 'a',
        attributes: {
          'data-mention-id': _.get(data, 'mention.name'),
          href: _.get(data, 'mention.link'),
          class: 'draft-mention',
        },
        style: {},
      };
    }
    if (entityType === '#mention') {
      const data = entity.getData();
      return {
        element: 'a',
        attributes: {
          'data-mention-id': _.get(data, 'mention.name'),
          href: _.get(data, 'mention.link'),
          class: 'draft-mention',
        },
        style: {},
      };
    }
    return undefined;
  },
};

const clipboardOptions: OptionsStateFromHTML = {
  customInlineFn: (element) => {
    const styleValue = element.getAttribute('style');
    let styleForEntity;
    if (!styleValue) return undefined;
    if (styleValue.indexOf('bold') > -1) {
      styleForEntity = 'BOLD';
    }
    return styleForEntity
      ? { type: 'STYLE', style: styleForEntity }
      : undefined;
  },
};

export default function PostRichTextEditor({
  initialData,
  setEditorValue,
  setEditorRawValue,
  isEdit = false,
}: PostRichTextEditorProps): ReactElement {
  const [{ plugins, Toolbar, MentionSuggestions }] = useState(() => {
    const toolbarPlugin = createToolbarPlugin();
    const linkifyPlugin = createLinkifyPlugin({ target: '_blank' });
    const mentionPlugin = createMentionPlugin({
      theme: mentionStyles,
      entityMutability: 'IMMUTABLE',
      mentionPrefix: '@',
      mentionTrigger: '@',
      supportWhitespace: true,
      mentionComponent: (mentionProps) => {
        const href = `/noum/${mentionProps.mention.link}`;
        return (
          <MentionLink to={href} title={mentionProps.mention.name}>
            {mentionProps.mention.name}
          </MentionLink>
        );
      },
    });
    const { Toolbar } = toolbarPlugin;
    const { MentionSuggestions } = mentionPlugin;
    const plugins = [toolbarPlugin, mentionPlugin, linkifyPlugin];
    return {
      plugins,
      Toolbar,
      MentionSuggestions,
    };
  });

  const init = useCallback(
    (data?: PostOutput, isEdit?: boolean) => {
      const decorators = flattenDeep(
        cleanList(plugins.map((plugin) => plugin.decorators)),
      ) as DraftDecorator[];
      const decorator = new CompositeDecorator(
        decorators?.filter((dec, index) => index !== 1),
      );

      if (data?.rawJSON && data?.rawJSON?.blocks && data.rawJSON?.entityMap) {
        return EditorState.createWithContent(
          convertFromRaw(data?.rawJSON),
          isEdit ? undefined : decorator,
        );
      }

      if (!data || !data?.text || data.text === '')
        return EditorState.createEmpty();

      const parsedText = returnParsedTagsText(data);
      if (!parsedText) return EditorState.createEmpty();
      const state = stateFromHTML(parsedText);
      const raw = parseRawWithMentions(convertToRaw(state));
      const contentState = convertFromRaw(raw);
      return EditorState.moveFocusToEnd(
        EditorState.createWithContent(
          contentState,
          isEdit ? undefined : decorator,
        ),
      );
    },
    [plugins],
  );
  const { t } = useTranslation();
  const { isMobile } = useBreakpoints();
  const [remountKey, setRemountKey] = useState(1);
  const ref = useRef<Editor>(null);
  const [editorState, setEditorState] = useState(() =>
    init(initialData, isEdit),
  );
  const [open, setOpen] = useState(false);
  const [suggestions, setSuggestions] = useState<MentionData[]>([]);

  useEffect(() => {
    if (isEdit === false) setEditorState(init(initialData, isEdit));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [initialData, isEdit]);

  useEffect(() => {
    if (editorState && !editorState.getDecorator() && !isEdit) {
      const newValue = remountKey + 1;
      setRemountKey(newValue);
    }
  }, [editorState, isEdit, remountKey]);

  const onOpenChange = useCallback((_open: boolean) => {
    setOpen(_open);
  }, []);

  const [searchUsers] = useGlobalSearchLazyQuery();

  const onSearchChange = useCallback(
    async ({ value }: { trigger: string; value: string }) => {
      const { data } = await searchUsers({
        variables: {
          query: value,
          limit: 10,
          entityType: EntityType.HomeNoum,
        },
      });
      const mentionSuggestions: MentionData[] =
        data?.globalSearch.data.map((record) => ({
          name: record.user.name || '',
          avatar: record.user.thumbnailUrl || OwnerDefaultImage,
          link: record.id,
          id: record.id,
          uid: record?.user?.id,
        })) || [];
      setSuggestions(mentionSuggestions);
    },
    [searchUsers],
  );

  useEffect(() => {
    if (setEditorValue) {
      const contentState = editorState.getCurrentContent();
      const editorPlainText = contentState.getPlainText();
      const markup =
        editorPlainText === '' ? undefined : stateToHTML(contentState, options);
      if (isEdit) {
        setEditorValue(markup || '');
        setEditorRawValue?.(convertToRaw(contentState));
      }
    }
  }, [editorState, isEdit, setEditorRawValue, setEditorValue]);

  const onHandlePastedText = (
    text: string,
    html: string | undefined,
    state: EditorState,
  ): DraftHandleValue => {
    if (!text || text === '') return 'not-handled';
    const sourceState = html
      ? stateFromHTML(html, clipboardOptions)
      : EditorState.createWithContent(
          ContentState.createFromText(text),
        ).getCurrentContent();
    const sourceRaw = parseRawWithMentions(convertToRaw(sourceState));
    const parsedState = convertFromRaw(sourceRaw);
    const newState: ContentState = Modifier.replaceWithFragment(
      state.getCurrentContent(),
      state.getSelection(),
      parsedState.getBlockMap(),
    );
    setEditorState(EditorState.push(state, newState, 'insert-fragment'));
    return 'handled';
  };

  const onFocus = useCallback(() => {
    setTimeout(() => ref.current!.focus(), 50);
  }, []);

  return (
    <Container
      data-testid="post-rich-text-editor"
      isEdit={isEdit}
      onClick={onFocus}
    >
      {isEdit && (
        <ToolbarContainer isMobile={isMobile}>
          <Toolbar>
            {(externalProps) => (
              <Fragment>
                <BoldButton {...externalProps} />
                <ItalicButton {...externalProps} />
                <UnderlineButton {...externalProps} />
                <Separator />
                <UnorderedListButton {...externalProps} />
                <OrderedListButton {...externalProps} />
                <Separator />
                <HeadlineOneButton {...externalProps} />
                <HeadlineTwoButton {...externalProps} />
              </Fragment>
            )}
          </Toolbar>
        </ToolbarContainer>
      )}
      <Editor
        placeholder={t(`noumena.create_post_placeholder`)}
        readOnly={!isEdit}
        editorKey="editor"
        editorState={editorState}
        onChange={setEditorState}
        plugins={plugins}
        ref={ref}
        handlePastedText={onHandlePastedText}
      />
      <MentionSuggestions
        open={open}
        onOpenChange={onOpenChange}
        suggestions={suggestions}
        onSearchChange={onSearchChange}
      />
    </Container>
  );
}
