import { useTranslation } from 'react-i18next';
import { useMemo } from 'react';
import {
  ButtonWrap,
  LoadMoreContainer,
  LoadMoreText,
} from '@/screens/Chamber/components/elements/Comments/styles';
import { type LoadMoreProps } from '@/screens/Chamber/components/elements/Comments/types';
import LoadMoreAvatar from '@/screens/Chamber/components/elements/Comments/LoadMoreAvatar';
import { type Comments, type ReplyOutput } from '@/apollo/generated/types';

const LoadMore = ({
  handleLoadMore,
  isReplies,
  count,
  replies,
}: LoadMoreProps) => {
  const { t } = useTranslation();
  const userList = useMemo(
    () =>
      (replies as ReplyOutput)?.userIdList ||
      ((replies || []) as Comments[])?.map((c) => ({ _id: c?.uid })) ||
      [],
    [replies],
  );
  const pictures = useMemo(
    () =>
      [...new Set(userList.map((u) => u?._id?.profile?.profilePicture))].slice(
        0,
        3,
      ) as string[],
    [userList],
  );

  return (
    <LoadMoreContainer isReply={isReplies}>
      <LoadMoreAvatar pictures={pictures} />
      <ButtonWrap onClick={handleLoadMore}>
        <LoadMoreText
          font="button-s"
          colorToken="--text-button-brand-primary-default"
        >
          {`${t('noumena.load.more.Show')} ${count} ${t(
            'noumena.load.more.earlier',
          )} `}{' '}
          {isReplies ? `${t('noumena.replies')}` : `${t('noumena.comments')}`}
        </LoadMoreText>
      </ButtonWrap>
    </LoadMoreContainer>
  );
};
export default LoadMore;
